<template>
  <div class="" id="submit">
    <form @submit.prevent="submit">
      <div class="title">
        <input type="text" placeholder="...10" v-model="form.title">
      </div>
      <div class="select">
        <select v-model="form.tab">
          <option value=""></option>
          <option value="share"></option>
          <option value="ask"></option>
          <option value="job"></option>
          <option value="dev"></option>
        </select>
      </div>
      <div>
        <textarea placeholder="...markdown" v-model="form.content"></textarea>
      </div>
      <div class="padding">
        <button class="btn"></button>
      </div>
    </form>
  </div>
</template>

<script>
  import api from '../api/'
  import utils from '../utils/utils'
  import comMixin from '../mixins/com-mixin'
  export default {
    mixins: [comMixin],
    name: 'submit',
    data () {
      return {
        form: {
          accesstoken: '', // token
          title: '', // 
          tab: '', // 
          content: '' // 
        }
      }
    },

    mounted () {
      this.$store.dispatch('commConf', {menu: false, back: true, title: ''})
      this.form.accesstoken = this.getUser.token
    },

    methods: {
      submit () {
        let {form, getUser, $router} = this
        if (!getUser.token) return $router.push({name: 'login'})  // 
        if (!form.title || !form.tab || !form.content) {
          return utils.layerMsg('')
        } else if (form.title.length < 10) {
          return utils.layerMsg('10')
        } else {
          api.creatTopic(form)
            .then(({data}) => {
              if (data && data.success) {
                $router.push({name: 'topic', params: {id: data.topic_id}})
              }
            })
            .catch((err) => {
              utils.layerMsg('' + err)
            })
        }
      }
    }
  }
</script>



