/**
 * 
 * @param  {String}  tab     
 * @param  {String}  good    
 * @param  {String}  top     
 * @param  {Boolean} isClass 
 * @return {String}          
 */
exports.getTabInfo = (tab, good, top, isClass) => {
  let str = ''
  let className = ''
  if (top) {
    str = ''
    className = 'top'
  } else if (good) {
    str = ''
    className = 'good'
  } else {
    switch (tab) {
      case 'share':
        str = ''
        className = 'share'
        break
      case 'ask':
        str = ''
        className = 'ask'
        break
      case 'job':
        str = ''
        className = 'job'
        break
      case 'all':
        str = ''
        className = 'all'
        break
      case 'good':
        str = ''
        className = 'good'
        break
      case 'dev':
        str = ''
        className = 'dev'
        break
      default:
        str = ''
        className = 'default'
        break
    }
  }
  return isClass ? className : str
}

/**
 * layer 
 * @param  {String} msg      
 * @param  {Number} interval 
 * @return {[type]}          undefined
 */
let timer  // 
exports.layerMsg = (msg = '', interval = 2000) => {   // 

  let layers = document.querySelectorAll('.layer-msg')  // layer
  if (layers.length) {  // 
    layers.forEach(function (el) {
      document.body.removeChild(el)
      clearTimeout(timer)
    })
  }

  let layer = document.createElement('div')
  layer.className = 'layer-msg layer-msg-show'
  layer.innerHTML = msg
  document.body.appendChild(layer)
  timer = setTimeout(() => {
    layer.className = 'layer-msg layer-msg-hide'
    clearTimeout(timer)
    let timer2 = setTimeout(() => {
      try {
        document.body.removeChild(layer)
      } catch (err) {}
      clearTimeout(timer2)
    }, 300)
  }, interval)
}



