<!--  -->
<template>
  <div class="addition-aside">
    <!--  -->
    <aside class="aside-left" ref="aside" @touchmove.stop>
      <!--  -->
      <div class="avatar flex-item">
        <router-link :to="{name: 'login'}" v-if="!getUser.token">
          <img src="https://avatars2.githubusercontent.com/u/16644635?v=4&s=460" alt="">
          <p></p>
        </router-link>
        <router-link :to="{name: 'user-info', params: {name: getUser.loginname}}" v-if="getUser.token">
          <img :src="getUser.avatar_url" alt="">
          <p>{{getUser.loginname}}</p>
        </router-link>

        <div class="collect-msg">
          <router-link :to="{name: 'collectTopic', params: {name: getUser.loginname}}">
            <i class="fa fa-star-o"></i>
          </router-link>
          <router-link :to="{name: 'message', params: {name: getUser.loginname}}">
            <i class="fa fa-bell-o" aria-hidden="true"></i>
          </router-link>
        </div>

      </div>

      <!--  -->
      <ul class="aside-menu">
        <li>
          <router-link :to="{name: 'Index', query: {tab: 'all'}}">
            <i class="fa fa-home" aria-hidden="true"></i>
            
          </router-link>
        </li>

        <li>
          <router-link :to="{name: 'Index', query: {tab: 'good'}}">
            <i class="fa fa-thumbs-up" aria-hidden="true"></i>
            
          </router-link>
        </li>

        <li>

        </i>
          <router-link :to="{name: 'Index', query: {tab: 'share'}}">
            <i class="fa fa-share-alt" aria-hidden="true"></i>
            
          </router-link>
        </li>

        <li>
          <router-link :to="{name: 'Index', query: {tab: 'ask'}}">
            <i class="fa fa-comments" aria-hidden="true"></i>
            
          </router-link>
        </li>

        <li>
          <router-link :to="{name: 'Index', query: {tab: 'job'}}">
            <i class="fa fa-users" aria-hidden="true"></i>
            
          </router-link>
        </li>

        <li>
          <router-link :to="{name: 'Index', query: {tab: 'dev'}}">
            <i class="fa fa-wrench" aria-hidden="true"></i>
            
          </router-link>
        </li>
        <div class="divider"></div>
        <!--  -->
        <li>
          <router-link :to="{name: 'about'}">
            <i class="fa fa-info-circle" aria-hidden="true"></i>
            
          </router-link>
        </li>
      </ul>

      <div class="auth">
        <!-- mrsix -->
      </div>
    </aside>

    <!--  -->
    <slot></slot>

    <!--  -->
    <div class="mask" v-show="mask" @touchstart.prevent @touchend="hideAside" :class="{active: isShow}"></div>
  </div>

</template>

<script>
  // muimint ui 
  import { once } from '../utils/dom'
  import comMixin from '../mixins/com-mixin'
  export default {
    mixins: [comMixin],
    name: 'aside-left',
    data () {
      return {
        animating: false,
        dragging: false,
        dragState: {},
        elWidth: null,
        aside: null,
        isShow: false,  // 
        mask: false // 
      }
    },
    props: {
      prevent: {
        type: Boolean,
        default: false
      },
      speed: {
        type: Number,
        default: 300
      }
    },

    created () {
      // this.$on('toggleAside', function () {
      //   this.showAside()
      // })
    },

    computed: {   // vuex 
      show_aside () {
        return this.$store.getters.showAside
      },
      hide_aside () {
        return this.$store.getters.hideAside
      }
    },

    watch: {
      'show_aside' (to, from) {   // vuex  
        this.showAside()
      },
      'hide_aside' (to, from) {
        this.hideAside()
      }
    },

    methods: {
      translate (offset, towards, speed) {  // 
        var el = this.aside
        if (!speed) { //   
          el.style.webkitTransition = '-webkit-transform ' + speed + 'ms cubic-bezier(0.165, 0.84, 0.44, 1)'
          el.style.webkitTransform = `translate3d(${offset}px, 0, 0)`
        } else {
          this.animating = true
          var dir = towards === 'right' ? this.elWidth : 0  // 
          this.mask = dir !== 0 // 

          el.style.webkitTransition = '-webkit-transform ' + speed + 'ms cubic-bezier(0.165, 0.84, 0.44, 1)'
          setTimeout(() => {
            el.style.webkitTransform = `translate3d(${dir}px, 0, 0)`
          }, 50)

          var called = false
          var transitionEndCallback = () => {   // 
            if (called) return
            this.animating = false
            called = true
            el.style.webkitTransition = ''
            this.isShow = towards === 'right'
          }
          once(el, 'webkitTransitionEnd', transitionEndCallback)  // 
          setTimeout(transitionEndCallback, speed + 100) // webkitTransitionEnd maybe not fire on lower version android.
        }
      },

      doOntouchStart (event) {  // 
        if (this.animating) return

        var dragState = this.dragState
        var touch = event.touches[0]

        dragState.startTime = new Date()
        dragState.startLeft = touch.clientX
        dragState.startTop = touch.clientY
      },

      doOntouchMove (event) {   // 
        var dragState = this.dragState
        var touch = event.touches[0]

        var currentLeft = touch.clientX   // 
        var currentTop = touch.clientY    // 

        var offsetLeft = currentLeft - dragState.startLeft  // X
        var offsetTop = currentTop - dragState.startTop     // Y

        var distenceX = Math.abs(offsetLeft)  //  
        var distenceY = Math.abs(offsetTop)   //  
        var maxOffset = this.elWidth  // equal as aside-left

        if (touch.clientX < 100 || distenceX < 5 || distenceX >= 5 && distenceX <= 1.73 * distenceY) {    //  1.100 2.(5px)(30)
          return
        }

        dragState.currentLeft = currentLeft   // X
        dragState.currentTop = currentTop     // Y

        var offset = Math.min(Math.max(-maxOffset + 1, offsetLeft), maxOffset)
        if ((offset > 0 && this.isShow) || (offset < 0 && !this.isShow)) return
        // 
        var translateDis = offset > 0 ? offset : maxOffset + offset
        this.translate(translateDis)
      },

      doOntouchEnd () {
        var dragState = this.dragState
        var dragDuration = new Date() - dragState.startTime   // 
        var maxOffset = this.elWidth   // 
        var towards = null  // 
        var offsetLeft = dragState.currentLeft - dragState.startLeft
        var distenceX = Math.abs(offsetLeft)
        var speed = this.speed

        if (dragDuration < 300 && dragState.currentLeft === undefined) { // tap
          this.dragState = {}
          return
        }
        if (dragDuration < 300 || distenceX > maxOffset / 2) {  // 
          towards = offsetLeft < 0 ? 'left' : 'right'
        }
        if (dragDuration > 300 && distenceX < maxOffset / 2) {  // 
          towards = offsetLeft > 0 ? 'left' : 'right'
        }
        // 
        this.translate(offsetLeft, towards, speed)
        this.dragState = {}
      },

      showAside () {  // 
        if (!this.isShow) this.translate(null, 'right', this.speed)
      },
      hideAside () {  // 
        if (this.isShow) this.translate(null, 'left', this.speed)
      },
      asideToggle () {  // 
        if (this.isShow) {
          this.translate(null, 'left', this.speed)
        } else {
          this.translate(null, 'right', this.speed)
        }
      }
    },

    mounted () {
      var el = this.$el
      this.aside = this.$refs.aside
      this.elWidth = this.$el.offsetWidth * 0.5  // equal as aside-left
      // touchstart
      el.addEventListener('touchstart', (event) => {
        if (this.prevent) {
          event.preventDefault()
        }
        if (this.animating) return
        this.dragging = true
        this.doOntouchStart(event)
      })
      // touchmove
      el.addEventListener('touchmove', (event) => {
        if (!this.dragging) return
        this.doOntouchMove(event)
      })
      // touchend
      el.addEventListener('touchend', (event) => {
        if (!this.dragging) return
        this.doOntouchEnd()
        this.dragging = false
      })
    }
  }
</script>

<style>

</style>



