<template>
  <div class="user-info">
    <div v-if="user.loginname">
      <!--  -->
      <div class="avatar-bg">
        <img src="https://i.imgur.com/6m23eL4.jpg" alt="">
        <div class="avatar flex-center-v">
          <img :src="user.avatar_url" alt="">
          <p>{{ user.loginname }}</p>
          <!--  -->
        </div>
      </div>
      <!-- user-content -->
      <div class="user-info-content">
        <!--  -->
        <div class="flex-center-h user-create">
          <p>{{ user.create_at | formatDate }}</p>
          <p class="theme">{{ user.score }}</p>
        </div>
        <!--  -->
        <ul class="tab-btn flex-center-h">
          <li :class="{selected: tabIndex === 0}" @click="tabIndex = 0"></li>
          <li :class="{selected: tabIndex === 1}" @click="tabIndex = 1"></li>
        </ul>
        <ul class="user-topics">
          <!--  -->
          <com-list
            v-show="tabIndex === 0"
            v-for="reply in user.recent_topics"
            :reply="reply"
          >
          </com-list>
          <!--  -->
          <com-list
            v-show="tabIndex === 1"
            v-for="reply in user.recent_replies"
            :reply="reply"
          >
          </com-list>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '../api/'
  import comList from '../components/com-list'
  import comMixin from '../mixins/com-mixin'
  export default {
    mixins: [comMixin],
    data () {
      return {
        tabIndex: 0,
        user: {}
      }
    },

    created () {
    },

    mounted () {
      this.getUserData()
    },

    methods: {
      getUserData () {  // 
        this.startLoading()  // 
        let {getUser, $store, $route} = this
        let name = $route.params.name
        let comm_conf = {menu: false, back: true, title: '', plus: false, logout: false}
        if (getUser && getUser.loginname === name) {  // 
          comm_conf = Object.assign(comm_conf, {logout: true})  // 
        }
        $store.dispatch('commConf', comm_conf)

        api.getUuser(name)
          .then(({data}) => {
            if (data && data.success) {
              this.user = data.data
              this.stopLoading()  // loading
            }
          })
          .catch((err) => {
            this.errShow(err)
          })
      }
    },

    watch: {  // 
      $route: 'getUserData'
    },

    components: {
      comList
    }
  }
</script>



