<template>
  <div class="replies">
    <form @submit.prevent="submit">
      <div class="border">
        <textarea :placeholder="loginname ? '' + loginname : '...(markdown)'" v-model="form.content"></textarea>
      </div>
      <div class="text-right">
        <button class="btn">{{btn}}</button>
      </div>
    </form>
  </div>
</template>

<script>
  import api from '../api/'
  import comMixin from '../mixins/com-mixin'
  import utils from '../utils/utils'
  export default {
    mixins: [comMixin],
    name: 'com-replies',
    data () {
      return {
        btn: '',
        form: {
          accesstoken: '',  // token
          content: '',  // 
          topic_id: '',  // id
          reply_id: ''  // id
        }
      }
    },
    props: {
      reply_id: {  // id
        type: String,
        default: ''
      },
      loginname: {  // 
        type: String,
        default: ''
      }
    },

    mounted () {
      this.form.accesstoken = this.getUser.token  // token
      this.form.topic_id = this.$route.params.id
      this.form.reply_id = this.reply_id
    },
    methods: {
      submit () {
        let {reply_id, form, loginname} = this
        if (form.content === '') {
          return utils.layerMsg('')
        }
        this.btn = '...'
        if (reply_id) {  // 
          form.content = `[@${loginname}](/user/${loginname}) ${form.content}`
        }
        form.content += `
        From [mrsix vue-cnode](https://github.com/Mr-six/my-cnode)`
        api.createReplies(form)
          .then(({data}) => {
            if (data && data.success) {
              this.btn = ''
              form.content = ''
              this.$emit('updata')
              utils.layerMsg('')
            }
          })
          .catch((err) => {
            this.btn = ''
            utils.layerMsg('' + err)
          })
      }
    }
  }
</script>



