<template>
  <div id="index">
    <!--  -->
    <topic-list :topices="topices"></topic-list>
  </div>
</template>

<script>
  // api
  import api from '../api/'
  import utils from '../utils/utils'
  import topicList from '../components/topic-list'
  import comMixin from '../mixins/com-mixin'
  export default {
    mixins: [comMixin],
    name: 'index',
    data () {
      return {
        scrollGet: true,
        topices: [],
        getConf: {    // 
          page: 1,
          tab: 'all',
          limit: 20,
          mdrender: true
        }
      }
    },

    mounted () {    // 
      this.startLoading()  // 
      let {getAlltopices, topices, getConf, $route} = this
      if (!$route.query.tab) {
        this.$router.replace({name: 'Index', query: {tab: 'all'}})
      }
      if ($route.query && $route.query.tab) {   //  
        getConf.tab = $route.query.tab
      }
      const session = sessionStorage
      let tab = getConf.tab
      if (session.getItem(tab)) {   // sessionStorage
        let _topices = JSON.parse(session.getItem(tab))
        _topices.forEach(function (el) {   // 
          topices.push(el)
        })
        this.stopLoading()  // loading
      } else {  // 
        getAlltopices()
      }

      this.$store.dispatch('commConf', {menu: true, back: false, title: utils.getTabInfo(tab, false, false, false), plus: true, logout: false})  // 
      // 
      window.addEventListener('scroll', this.getScrollData, false)
    },

    computed: {

    },

    methods: {
      getAlltopices () {  // API 
        if (this.$route.name !== 'Index') return
        const session = sessionStorage
        let {topices, getConf} = this
        let tab = getConf.tab
        api.getTopics(getConf)
          .then(({data}) => {   // 
            if (data && data.success) {
              data.data.forEach(function (el) {   // 
                topices.push(el)
              })
              this.stopLoading()  // loading
              this.scrollGet = true
            }
            session.setItem(tab, JSON.stringify(topices))   // sessionStorage
          })
          .catch((err) => {   // 
            this.errShow(err)
          })
      },

      getScrollData () {  // 
        if (this.scrollGet) {
          let totalHeight = document.body.scrollHeight   // 
          let devHeight = document.documentElement.clientHeight  // 
          let pageScrollTop = document.documentElement.scrollTop  // 
          if (totalHeight - devHeight - pageScrollTop <= 200) {  // 
            this.getConf.page++
            this.getAlltopices()
            this.scrollGet = false
          }
        }
      },

      beforeRouteEnter (to, from, next) {   //  
        const session = sessionStorage
        if (from.name !== 'topic') {
          // 
          if (session.tab) {
            session.removeItem('all')
            session.removeItem('good')
            session.removeItem('ask')
            session.removeItem('job')
            session.removeItem('share')
          }
        }
        next()
      }
    },

    watch: {
      // 
      '$route' (to, from) {
        // 
        let com_conf = {}
        if (to.query && to.query.tab) {
          this.startLoading()  // 
          let query = to.query.tab  // 
          this.getConf.tab = to.query.tab
          this.topices = []
          com_conf = {title: utils.getTabInfo(query, false, false, false)}  // 
        } else {
          com_conf = Object.assign(com_conf, {menu: true, back: false})
        }
        this.getConf.page = 1
        this.getAlltopices()
        this.$store.dispatch('commConf', com_conf)  // 
      }
    },

    components: {
      topicList
    }
  }
</script>




