<template>
  <div id="collect">
    <topic-list :topices="topices"></topic-list>
  </div>
</template>

<script>
  import api from '../api/'
  // import utils from '../utils/utils'
  import topicList from '../components/topic-list'
  import comMixin from '../mixins/com-mixin'

  export default {
    mixins: [comMixin],
    name: 'collectTopic',
    data () {
      return {
        topices: [],
        tab: ''
      }
    },

    mounted () {    // 
      this.startLoading()  // 
      let {getCollectTopic, topices, tab, $store} = this
      $store.dispatch('commConf', {menu: false, back: true, title: tab})  // 

      const session = sessionStorage
      if (session.getItem(tab)) {   // sessionStorage
        let _topices = JSON.parse(session.getItem(tab))
        _topices.forEach(function (el) {   // 
          topices.push(el)
        })
        this.stopLoading()  // loading
      } else {  // 
        getCollectTopic()
      }
    },

    methods: {
      getCollectTopic () {  // API 
        const session = sessionStorage
        let {topices, tab, getUser} = this
        api.getCollectTopic(getUser.loginname)
          .then(({data}) => {   // 
            if (data && data.success) {
              data.data.forEach(function (el) {   // 
                topices.push(el)
              })
              this.stopLoading()  // loading
            }
            session.setItem(tab, JSON.stringify(topices))   // sessionStorage
          })
          .catch((err) => {   // 
            this.errShow(err)
          })
      }
    },
    components: {
      topicList
    }

  }
</script>



