// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import './scss/main.scss'

// mint-ui
// import Mintui from 'mint-ui'
// import 'mint-ui/lib/style.css'

// 
import comLoading from './components/com-loading' // loading
import comErr from './components/com-err'  // 
Vue.component('com-loading', comLoading)
Vue.component('com-err', comErr)

// 
import * as filters from './utils/filters'
Object.keys(filters).forEach(k => Vue.filter(k, filters[k])) // 

// Vue.use(Mintui)

// 
router.beforeEach(({meta, path, fullPath}, from, next) => {
  const {auth = false} = meta
  const isLogin = Boolean(store.getters.getUser.token)  // 
  if (auth && !isLogin && path !== '/login') {
    let to = {path: '/login', query: { redirect: fullPath }}  // 
    return next(to)  // 
  }
  next()
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  ...App
})



